.ProductDetails {
  background-color: #000000db;
  width: 100vw;
  max-width: 100%;
  padding: 6vmax;
  box-sizing: border-box;
  display: flex;
}

.ProductDetails > div {
  width: 50%;
  display:flexbox;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
  border: 1px solid rgb(255, 255, 255);
}

.ProductDetails > div:last-child {
  align-items: flex-start;
}

.CarouselImage {
 padding-left: 7rem;
  width: 25vmax;
  height: 300px;
}

.detailsBlock-1 > h2 {
  color: rgb(255, 255, 255);
  font: 600 1.6vmax "Roboto";
}

.detailsBlock-1 > p {
  color: rgba(255, 255, 255, 0.582);
  font: 200 0.6vmax cursive;
}

.detailsBlock-2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-top: 1px solid rgba(255, 255, 255, 0.205);
  border-bottom: 1px solid rgba(255, 255, 255, 0.205);
  width: 70%;
  padding: 1vmax 0;
}

.detailsBlock-2-span {
  font: 200 0.8vmax cursive;
  color: rgba(255, 255, 255, 0.699);
}

.detailsBlock-3 {
  width: 70%;
}

.detailsBlock-3 > h1 {
  color: rgb(9, 9, 9);
  font: 400 1.8vmax "Franklin Gothic Medium";
  margin: 1vmax 0;
}
.detailsBlock-3-1 {
  display: flex;
  align-items: center;
}

.detailsBlock-3-1-1 > button {
  border: none;
  background-color: rgba(255, 255, 255, 0.616);
  padding: 0.5vmax;
  cursor: pointer;
  color: rgb(255, 255, 255);
  transition: all 0.5s;
}
.detailsBlock-3-1-1 > button:hover {
  background-color: rgb(221, 255, 183);
}

.detailsBlock-3-1-1 > input {
  border: none;
  padding: 0.5vmax;
  width: 4vmax;
  text-align: center;
  outline: none;
  font: 400 0.8vmax "Roboto";
  color: rgb(255, 255, 255);
}

.detailsBlock-3-1 > button:last-child {
  border: none;
  cursor: pointer;
  color: rgb(255, 255, 255);
  transition: all 0.5s;
  background-color: rgb(221, 255, 183);
  font: 500 0.7vmax "Roboto";
  border-radius: 20px;
  padding: 0.5vmax 2vmax;
  margin: 1vmax;
  outline: none;
}

.detailsBlock-3-1 > button:last-child:hover {
  background-color: rgb(140, 214, 61);
}

.detailsBlock-3 > p {
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  padding: 1vmax 0;
  color: rgb(255, 255, 255);
  font: 400 1vmax "Roboto";
  margin: 1vmax 0;
}

.detailsBlock-4 {
  color: rgba(255, 255, 255, 0.897);
  font: 500 1.2vmax sans-serif;
}

.detailsBlock-4 > p {
  color: rgba(255, 255, 255, 0.534);
  font: 300 0.8vmax sans-serif;
}

.submitReview {
  border: none;
  background-color: rgb(221, 255, 183);
  font: 500 0.7vmax "Roboto";
  border-radius: 20px;
  padding: 0.6vmax 2vmax;
  margin: 1vmax 0;
  color: rgb(255, 255, 255);
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
}
.submitReview:hover {
  background-color: rgb(126, 197, 45);
  transform: scale(1.1);
}

.submitDialog {
  color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
}
.submitDialogTextArea {
  border: 1px solid rgba(255, 255, 255, 0.759);
  margin: 1vmax 0;
  outline: none;
  padding: 1rem;
  font: 300 1rem "Roboto";
}

.reviewsHeading {

  color: #ffffffbe;
  font: 500 1.4vmax "Roboto";
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.226);
  padding: 1vmax;
  width: 20vmax;
  margin: auto;
  margin-bottom: 4vmax;
}
.reviews {
  background-color:#ffffff79;
  display: flex;
  overflow: auto;
}

.reviewCard {
  flex: none;
  box-shadow: 0 0 5px rgb(0, 0, 0);
  border: 1px solid rgb(0, 0, 0);
  width: 30vmax;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1vmax;
  padding: 3vmax;
}
.reviewCard > img {
  width: 5vmax;
}
.reviewCard > p {
  color: rgba(255, 255, 255, 0.836);
  font: 600 0.9vmax "Roboto";
}
.reviewCardComment {
  color: rgba(255, 255, 255, 0.763);
  font: 300 0.8vmax cursive;
}

.noReviews {
  font: 400 1.3vmax "Gill Sans";
  text-align: center;
  color: rgba(255, 255, 255, 0.548);
}

@media screen and (max-width: 950px){
  .CarouselImage {
    margin-left: -7rem ;
    width: 33vmax;
    height: 500px;
   }
} 


